'use client';

import { useState } from 'react';
import { supabase } from '@/lib/supabaseClient';

export default function RegisterPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [role, setRole] = useState<'student' | 'entity'>('student');
  const [message, setMessage] = useState('');

  const handleRegister = async () => {
    const { data, error } = await supabase.auth.signUp({
      email,
      password,
    });

    if (error) {
      setMessage(error.message);
      return;
    }

    const { error: dbError } = await supabase.from('users').insert([
      { email, role },
    ]);

    if (dbError) {
      setMessage(dbError.message);
      return;
    }

    setMessage('Registrazione completata! Controlla la tua email per confermare.');
  };

  return (
    <div className="max-w-md mx-auto mt-10 p-4 border rounded shadow">
      <h1 className="text-2xl font-bold mb-4">Registrati</h1>
      <input
        className="w-full border p-2 mb-2"
        type="email"
        placeholder="Email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
      />
      <input
        className="w-full border p-2 mb-2"
        type="password"
        placeholder="Password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
      />
      <select
        className="w-full border p-2 mb-2"
        value={role}
        onChange={(e) => setRole(e.target.value as 'student' | 'entity')}
      >
        <option value="student">Studente</option>
        <option value="entity">Ente scolastico</option>
      </select>
      <button
        className="w-full bg-blue-500 text-white p-2 rounded"
        onClick={handleRegister}
      >
        Registrati
      </button>
      {message && <p className="mt-2 text-red-500">{message}</p>}
    </div>
  );
}
